<script setup lang="tsx">import { ref as _ref, computed as _computed } from 'vue';

import { nextTick, onMounted, useSlots } from 'vue'
import { useI18n } from 'vue-i18n'
const { t: $t } = useI18n()
import MiniDialog from '@/components/dialog/MiniDialog.vue'
import BaseIcon from '@/components/BaseIcon.vue'
import BaseButton from '@/components/BaseButton.vue'
import { debounce } from '@/utils'
import PopConfirm from '@/components/PopConfirm.vue'
import Empty from '@/components/Empty.vue'
import Pagination from '@/components/base/Pagination.vue'
import Checkbox from '@/components/base/checkbox/Checkbox.vue'
import DeleteIcon from '@/components/icon/DeleteIcon.vue'
import Dialog from '@/components/dialog/Dialog.vue'
import BaseInput from '@/components/base/BaseInput.vue'
import { ENV, Host } from '@/config/env.ts'
import { Sort } from '@/types/enum.ts'

const props = defineProps({
  loading: { type: Boolean, default: true },
  showToolbar: { type: Boolean, default: true },
  showCheckbox: { type: Boolean, default: false },
  showPagination: { type: Boolean, default: true },
  exportLoading: { type: Boolean, default: false },
  importLoading: { type: Boolean, default: false },
  request: { type: Function },
  list: null
})

const emit = defineEmits(["add", "click", "import", "export", "del", "sort"])

let listRef: any = _ref()
let showCheckbox = _ref(false)

function scrollToBottom() {
  nextTick(() => {
    listRef.value?.scrollTo(0, listRef.value.scrollHeight)
  })
}

function scrollToTop() {
  nextTick(() => {
    listRef.value?.scrollTo(0, 0)
  })
}

function scrollToItem(index: number) {
  nextTick(() => {
    listRef.value?.children[index]?.scrollIntoView({ block: 'center', behavior: 'smooth' })
  })
}

let selectIds = _ref([])
let selectAll = _computed(() => {
  return !!selectIds.value.length
})

function toggleSelect(item) {
  let rIndex = selectIds.value.findIndex(v => v === item.id)
  if (rIndex > -1) {
    selectIds.value.splice(rIndex, 1)
  } else {
    selectIds.value.push(item.id)
  }
}

function toggleSelectAll() {
  if (selectAll.value) {
    selectIds.value = []
  } else {
    selectIds.value = params.value.list.map(v => v.id)
  }
}

let showSortDialog = _ref(false)
let showSearchInput = _ref(false)
let showImportDialog = _ref(false)

const closeImportDialog = () => (showImportDialog.value = false)

function sort(type: Sort) {
  if ([Sort.reverse, Sort.random].includes(type)) {
    emit('sort', type, params.value.pageNo, params.value.pageSize)
  } else {
    emit('sort', type, 1, params.value.total)
  }
  showSortDialog.value = false
}

function handleBatchDel() {
  emit('del', selectIds.value)
  selectIds.value = []
}

const s = useSlots()

defineExpose({
  scrollToBottom,
  scrollToItem,
  closeImportDialog,
  getData,
})

let loading2 = _ref(false)

let params = _ref({
  pageNo: 1,
  pageSize: 50,
  total: 0,
  list: [],
  sortType: null,
  searchKey: '',
})

function search(key: string) {
  if (!params.value.searchKey) {
    params.value.pageNo = 1
  }
  params.value.searchKey = key
  getData()
}

function cancelSearch() {
  params.value.searchKey = ''
  showSearchInput.value = false
  getData()
}

async function getData() {
  if (props.request) {
    loading2.value = true
    let { list, total } = await props.request(params.value)
    params.value.list = list
    params.value.total = total
    loading2.value = false
  } else {
    params.value.list = props.list ?? []
  }
}

function handlePageNo(e) {
  params.value.pageNo = e
  getData()
  scrollToTop()
}

onMounted(async () => {
  getData()
})

defineRender(() => {
  const d = item => (
    <Checkbox modelValue={selectIds.value.includes(item.id)} onChange={() => toggleSelect(item)} size="large" />
  )

  return (
    <div class="base-table flex flex-col gap-3">
      {props.showToolbar && (
        <div>
          {showSearchInput.value ? (
            <div class="flex gap-4">
              <BaseInput
                clearable
                modelValue={params.value.searchKey}
                onUpdate:modelValue={debounce(e => search(e), 500)}
                class="flex-1"
                autofocus
              >
                {{
                  subfix: () => <IconFluentSearch24Regular class="text-lg text-gray" />,
                }}
              </BaseInput>
              <BaseButton onClick={cancelSearch}>{$t('cancel')}</BaseButton>
            </div>
          ) : (
            <div class="flex justify-between items-center">
              {showCheckbox.value ? (
                <div class="flex gap-2 items-center">
                  <Checkbox
                    disabled={!params.value.list.length}
                    onChange={() => toggleSelectAll()}
                    modelValue={selectAll.value}
                    size="large"
                  />
                  <span>
                    {selectIds.value.length} / {params.value.total}
                  </span>
                </div>
              ) : (
                <div>{params.value.total}{$t('total_items')}</div>
              )}

              <div class="flex gap-2 relative">
                {selectIds.value.length && showCheckbox.value ? (
                  <PopConfirm title={$t('confirm_delete_selected')} onConfirm={handleBatchDel}>
                    <BaseButton type="info">{$t('confirm')}</BaseButton>
                  </PopConfirm>
                ) : null}

                <BaseIcon onClick={() => (showCheckbox.value = !showCheckbox.value)} title={$t('batch_delete')}>
                  <DeleteIcon />
                </BaseIcon>

                <BaseIcon onClick={() => (showImportDialog.value = true)} title={$t('import')}>
                  <IconSystemUiconsImport />
                </BaseIcon>
                <BaseIcon onClick={() => emit('export')} title={$t('export')}>
                  {props.exportLoading ? <IconEosIconsLoading /> : <IconPhExportLight />}
                </BaseIcon>
                <BaseIcon onClick={() => emit('add')} title={$t('add_word')}>
                  <IconFluentAdd20Regular />
                </BaseIcon>
                <BaseIcon
                  disabled={!params.value.list.length}
                  title={$t('change_order')}
                  onClick={() => (showSortDialog.value = !showSortDialog.value)}
                >
                  <IconFluentArrowSort20Regular />
                </BaseIcon>
                <BaseIcon
                  disabled={!params.value.list.length}
                  onClick={() => (showSearchInput.value = !showSearchInput.value)}
                  title={$t('search')}
                >
                  <IconFluentSearch20Regular />
                </BaseIcon>
                <MiniDialog
                  modelValue={showSortDialog.value}
                  onUpdate:modelValue={e => (showSortDialog.value = e)}
                  style="width: 8rem;"
                >
                  <div class="mini-row-title">{$t('list_order_setting')}</div>
                  <div class="flex flex-col gap2 btn-no-margin">
                    <BaseButton onClick={() => sort(Sort.reverse)}>{$t('reverse_current_page')}</BaseButton>
                    <BaseButton onClick={() => sort(Sort.reverseAll)}>{$t('reverse_all')}</BaseButton>
                    <div class="line"></div>
                    <BaseButton onClick={() => sort(Sort.random)}>{$t('random_current_page')}</BaseButton>
                    <BaseButton onClick={() => sort(Sort.randomAll)}>{$t('random_all')}</BaseButton>
                  </div>
                </MiniDialog>
              </div>
            </div>
          )}
        </div>
      )}

      <div class="relative flex-1 overflow-hidden">
        {params.value.list.length ? (
          <div class="overflow-auto h-full" ref={e => (listRef.value = e)}>
            {params.value.list.map((item, index) => {
              return (
                <div class="list-item-wrapper" key={item.word}>
                  {s.default({
                    checkbox: showCheckbox.value ? d : () => void 0,
                    item,
                    index: params.value.pageSize * (params.value.pageNo - 1) + index + 1,
                  })}
                </div>
              )
            })}
          </div>
        ) : !loading2.value ? (
          <Empty />
        ) : null}
        {loading2.value && (
          <div class="absolute top-0 left-0 bottom-0 right-0 bg-black bg-op-10  center text-4xl">
            <IconEosIconsLoading color="gray" />
          </div>
        )}
      </div>

      {props.showPagination && (
        <div class="flex justify-end">
          <Pagination
            currentPage={params.value.pageNo}
            onUpdate:current-page={handlePageNo}
            pageSize={params.value.pageSize}
            onUpdate:page-size={e => (params.value.pageSize = e)}
            pageSizes={[20, 50, 100, 200]}
            layout="total,sizes"
            total={params.value.total}
          />
        </div>
      )}

      <Dialog modelValue={showImportDialog.value} onUpdate:modelValue={closeImportDialog} title={$t('import_tutorial')}>
        <div className="w-100 p-4 pt-0">
          <div>{$t('import_follow_template')}</div>
          <div class="color-red">{$t('import_word_required')}</div>
          <div>{$t('import_translation_hint')}</div>
          <div>
            {$t('import_example_hint')}<span class="color-red">{$t('two')}</span>{$t('lines')}
          </div>
          <div>
            {$t('import_phrase_hint')}<span class="color-red">{$t('two')}</span>{$t('lines')}
          </div>
          <div>{$t('import_other_hint')}</div>
          <div class="mt-6">
            {$t('template_download')}：<a href={`${ENV.RESOURCE_URL}/libs/单词导入模板.xlsx`}>{$t('word_import_template')}</a>
          </div>
          <div class="mt-4">
            <BaseButton
              onClick={() => {
                let d: HTMLDivElement = document.querySelector('#upload-trigger')
                d.click()
              }}
              loading={props.importLoading}
            >
              {$t('import')}
            </BaseButton>
            <input
              id="upload-trigger"
              type="file"
              accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
              onChange={e => emit('import', e)}
              class="w-0 h-0 opacity-0"
            />
          </div>
        </div>
      </Dialog>
    </div>
  )
})
</script>
<style scoped lang="scss"></style>
